body.explorer {
  .path-rotation:before {
    display: none;
  }
}

.header {
  width: 100%;
  position: absolute;
  height: 100px;

  &.isFixed {
    position: fixed;
    background: $white;

    .hamburglar {

      .path-rotation:before,
      .burger-bun-top,
      .burger-bun-bot,
      .burger-filling {
        background: $black;
      }

      .svg-ring {
        stroke: $black;
      }
    }

    .header__phoneImage {
      fill: $black;
    }

    .header__phone a {
      color: $black;
    }
  }
}

.header__content {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  height: 100%;
  align-items: center;
  margin: 0 auto;
  max-width: 1900px;
  padding: 0 40px;
}

